#movieCard {
  background-color: #1e334c;
  min-height: 600px;
  margin-bottom: 25px;
  border-color: #fffafa;
  color: #fffafa;
}

#cardTitle{
  font-family: 'Teko', Arial, Helvetica, sans-serif;
  text-transform: uppercase;
  font-size: 45px;
  font-weight: lighter;
}

#cardText{
  font-family: 'Lato', Helvetica, Arial, sans-serif;
  font-weight: 300;
}

#moreButton{
  position: absolute;
  bottom: 0;
  margin-bottom: 25px;
  color: #fffafa
}